@charset "UTF-8";
.cursor {
  position: absolute;
  width: 40px;
  height: 40px;
  background-image: url(/public/clip.png);
  background-size: cover;
  pointer-events: none;
  z-index: 9999;
  font-size: 0.8rem;
}
.cursor .noti {
  position: relative;
  width: 4em;
  top: 40px;
  left: calc(-1em + 8px);
  background-color: #fff;
  border: 1px solid black;
  border-radius: 3px;
}
.cursor .noti::before {
  content: "";
  position: absolute;
  top: -10px;
  left: calc(2em - 4px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 3px 10px 3px; /* 말풍선 꼬리의 크기 조정 */
  border-color: transparent transparent #fff transparent; /* 아래쪽 삼각형 색상을 말풍선 배경색과 동일하게 설정 */
  z-index: 1;
}
.cursor .noti::after {
  content: "";
  position: absolute;
  top: -10px;
  left: calc(2em - 6px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 5px 10px 5px; /* 말풍선 꼬리의 크기 조정 */
  border-color: transparent transparent black transparent; /* 아래쪽 삼각형 색상을 말풍선 배경색과 동일하게 설정 */
  z-index: 0;
}

