.default {
  position: relative;
  min-width: 70px;
  min-height: 2rem;
  background: #cfdef5;
  border: 2px solid #4659ad;
  cursor: pointer;
  border-radius: 0.75em;
  transform-style: preserve-3d;
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), background 150ms cubic-bezier(0, 0, 0.58, 1);
}
.default::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #374b8f;
  border-radius: inherit;
  box-shadow: 0 0 0 2px #4659ad, 0 0.625em 0 0 rgba(0, 0, 0, 0.1098039216);
  transform: translate3d(0, 0.5em, -1em);
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), box-shadow 150ms cubic-bezier(0, 0, 0.58, 1);
}
.default:hover {
  background-color: #89b4d8;
  transform: translate(0, 0.2em);
}
.default:hover::before {
  box-shadow: 0 0 0 2px #4659ad, 0 0.5em 0 0 rgba(0, 0, 0, 0.1098039216);
  transform: translate3d(0, 0.3em, -1em);
}
.default:focus {
  border: 2px solid #4659ad;
  background-color: #89b4d8;
  transform: translate(0, 0.2em);
}
.default:focus::before {
  box-shadow: 0 0 0 2px #4659ad, 0 0.5em 0 0 rgba(0, 0, 0, 0.1098039216);
  transform: translate3d(0, 0.3em, -1em);
}
.default:active {
  background-color: #294a92;
  transform: translate(0em, 0.5em);
}
.default:active::before {
  box-shadow: 0 0 0px 2px #4659ad, 0 0 rgba(0, 0, 0, 0.1098039216);
  transform: translate3d(0, 0em, -1em);
}

